<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isshow">
      <el-form :model="form">
        <el-form-item label="规格名称" label-width="120px">
          <el-input v-model="form.specsname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item
          label="规格属性"
          label-width="120px"
          v-for="(item,index) in attrsArr"
          :key="index"
        >
          <div class="spcesNamebox">
            <div class="inp-box">
              <el-input v-model="item.val" autocomplete="off"></el-input>
            </div>
            <el-button @click="addArr" type="primary" v-if="index==0">新增属性</el-button>
            <el-button @click="delArr" type="danger" v-else>删 除</el-button>
          </div>
        </el-form-item>

        <el-form-item label="状态" label-width="120px">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add" v-if="info.title=='商品规格添加'">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import {
  reqSpcesAdd,
  reqSpcesListOne,
  reqSpcesUpdate
} from "../../../util/request";
import { alertSuccess, alertWarning } from "../../../util/alert";
export default {
  props: ["info"],
  computed: {
    ...mapGetters({
      list: "spces/spces",
      total: "spces/total"
    })
  },
  components: {},
  data() {
    return {
      form: {
        specsname: "",
        attrs: "[]",
        status: 1
      },
      attrsArr: [{ val: "" }]
    };
  },
  methods: {
    //验证
    checked() {
      return new Promise((resolve, reject) => {
        //验证数据是否均不为空
        if (this.form.specsname === "") {
          alertWarning("规格名称不能为空");
          return;
        }
        if (this.form.attrs === "[]") {
          alertWarning("规格属性不能为空");
          return;
        }
        resolve();
      });
    },
    addArr() {
      this.attrsArr.push({ val: "" });
    },
    delArr(index) {
      this.attrsArr.splice(index, 1);
    },
    add() {
      this.checked().then(() => {
        this.form.attrs = JSON.stringify(
          this.attrsArr.map(item => {
            return item.val;
          })
        );
        reqSpcesAdd(this.form).then(res => {
          if (res.data.code === 200) {
            this.cancel();
            this.empty();
            alertSuccess(res.data.msg);
            this.requsetSpcesList();
            this.requsetSpcesTotal();
          }
        });
      });
    },
    empty() {
      this.form = {
        specsname: "",
        attrs: "[]",
        status: 1
      };
      this.attrsArr = [{ val: "" }];
    },
    update() {
      this.checked().then(() => {
        this.form.attrs = JSON.stringify(
          this.attrsArr.map(item => {
            return item.val;
          })
        );

        reqSpcesUpdate(this.form).then(res => {
          if (res.data.code === 200) {
            this.empty();
            this.cancel();
            alertSuccess(res.data.msg);
            this.requsetSpcesList();
            this.requsetSpcesTotal();
          }
        });
      });
    },
    getOne(id) {
      reqSpcesListOne(id).then(res => {
        this.form = res.data.list[0];
        // console.log(res.data.list[0].attrs)
        this.attrsArr = JSON.parse(this.form.attrs).map(item => ({
          val: item
        }));
        // console.log(this.attrsArr)
      });
    },
    cancel() {
      this.info.isshow = false;
    },
    ...mapActions({
      requsetSpcesList: "spces/requsetSpcesList",
      requsetSpcesTotal: "spces/requsetSpcesTotal"
    })
  },
  mounted() {
    this.requsetSpcesList();
  }
};
</script>
<style scoped>
.spcesNamebox {
  display: flex;
}
.inp-box {
  flex: 1;
}
</style>


