<template>
<div>
 <el-button type="primary" @click="willAdd">添加</el-button>
 <v-add :info='info' ref="add"></v-add>
   <v-list @edit='willUpdate($event)'></v-list>
</div>
</template>
<script>
import vList from './components/list.vue'
import vAdd from './components/add.vue'
export default {
components:{
    vList,
    vAdd
 },
data () {
 return {
     info:{
         isshow:false,
         title:'商品规格添加'
     }
 }
},
methods:{
    willAdd(){
        this.info={
         isshow:true,
         title:'商品规格添加'
     };
     this.$refs.add.empty()
    },
    willUpdate(id){
         this.info={
         isshow:true,
         title:'商品规格修改'
     };
     this.$refs.add.getOne(id)
    }
},
mounted(){
}
}
</script>
<style scoped>
</style>


